.canvas {
  border: 1px solid black;
}

.wrapper {
  display: grid;
  /* align-items: center; */
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr;
}

.actionsElements {
  display: grid;
  align-items: center;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  padding-top: 12px;
}

table {
  align-items: center;
  text-align: center;
  align-self: start;
  width: 100%;
  border: 1px solid black;
}

.coordinatesInputForm {
  display: flex;
  flex-direction: column;
}
.inputActions {
  width: 50%;
  align-self: start;
  padding-left: 30%;
}

.save {
  width: 100%;
}

tr {
  border-bottom: 1px solid;
  border-collapse: collapse;
  margin: 25px 0;
  min-width: 400px;
  border-radius: 3px 3px 0 0;
  overflow: hidden;
}

th,
td {
  padding: 12px 15px;
  text-align: center;
}

tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

.btn {
  width: 50%;
  border: 1px;
  /* outline: none; */
}

.coordinateElement {
  /* font-size: 22px; */
  /* text-align: center; */
  align-content: center;
  /* display: table; */
  margin-left: auto;
  margin-right: auto;
}

.symbolsCoordinates {
  display: grid;
  /* grid-template-rows: 1fr 1fr; */
  text-align: center;
  grid-template-columns: 1fr 1fr;
}

.coordinates {
  display: grid;
  text-align: center;
  grid-template-columns: 1fr 1fr;
}

.pUnit {
  border: 1px solid black;
}

.deleteContainer {
  display: grid;
  grid-template-rows: 1fr;
  text-align: center;
  grid-template-columns: 1fr;
}
